@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);

/********************NAVBAR*************************/
nav{
    z-index: 2;
    height: 50px;
  }

  #name {
      right: 15px;
  }

  .name-logo {
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }

  .logo {
    width: 4rem;
    height: 3rem;
    position: absolute;
    opacity: .5;
    top: 2px;
    left: 30px;
  }
  
  /*----NAV-LINKS----*/
  
  .navbar-nav {
    position: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
  }
  
  nav a.nav-link {
    font-size: 1rem;
    font-weight: 500;
    letter-spacing: 0.0375rem;
    cursor: pointer;
  }
  
  nav h1 {
    color: var(--primary-purple);
  }
  
  nav ul li {
    left: 30px;
  }
  
  .navbar-nav>li>a {
    margin-right: 0.75rem;
    margin-left: 0.75rem;
    color: var(--primary-white) !important;
    border-top: 0.1875rem solid var(--primary-dark);
  }
  
  .navbar-nav>li>a.active, .navbar-nav>li>a.active:focus {
    border-top: 0.1875rem solid var(--primary-purple);
  }
  
  .navbar-nav>li>a:hover {
    border-top: 0.1875rem solid var(--primary-purple);
  }
  

/********************HEADER*************************/

.header-wrapper {
    position: relative;
    background: url(/react-portfolio/static/media/bg.ba272f3a.jpg) no-repeat;
    background-position: center;
    background-size: cover;
    height: 100vh;
    
}
  
  .main-info {
    display: -webkit-flex;
    display: flex;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    text-align: center;
    font-family: "Roboto Condesed", sans-serif;
    z-index: 2;
  }
  
  .main-info h1 {
    color: var(--primary-purple);
    font-size: 3rem;
  }
  
  @media(max-width: 768px) {
    .main-info h1 {
      font-size: 1rem !important;
    }
  }
  /*----TYPED-REACT----*/
  
  .typed-text{
    font-size: 2rem;
    color: var(--primary-white);
  }
  
  /*----MAIN-BTN----*/
  .btn-main-contact {
    border: 1px solid var(--primary-purple);
    text-transform: uppercase;
    border-radius: 0;
    padding: 0.65rem 0.75rem 0.75rem 0.75rem ;
    color: var(--primary-white);
    margin-top: 2rem;
    z-index: 2;
  }
  
  .btn-main-contact:hover {
    text-decoration: none;
    background-color: var(--primary-purple);
    color: var(--primary-white);
    transition: .2s ease-out;
  }
:root {
  --primary-white: #fff;
  --primary-purple: #A117F2;
  --primary-dark: rgba(255, 255, 255, 0);
}

body {
