/* button effects */
.myButton {
	box-shadow: 0px 0px 23px 1px #9fb4f2;
	background:linear-gradient(to bottom, #7892c2 5%, #ba69f0 100%);
	background-color:#7892c2;
	border-radius:9px;
	cursor: pointer;
	color:#ffffff;
	font-family:Arial;
	font-size:12px;
	padding:6px 23px;
	text-decoration:none;
	text-shadow:0px 2px 15px #283966;
    width: 250px;
}
.myButton:hover {
	background:linear-gradient(to bottom, #ba69f0 5%, #7892c2 100%);
	background-color:#ba69f0;
}
.myButton:active {
	position:relative;
	top:1px;
}
